body,
input {
  font-family: "Poppins", sans-serif; }

.barra-loging {
  background: #444;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.container {
  position: relative;
  width: 100%;
  background-color: #fff;
  min-height: 100vh;
  overflow: hidden; }

.forms-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.signin-signup {
  position: absolute;
